<template>
<div class="main-filter-title">Категории:</div>

<div class="filter-row">

      <div class="filter-container">
            <div class="form-group">
                  <div class="form-block">
                        <div class="form-group-title">Тип недвижимости:</div>
                        <div class="select-wrapper"> 
                              <select name="select-category" id="" class="select" v-model.number="currentCategoryId">
                                    <option value="0">показать все</option>
                                    <option :value="item.id" v-for="item in categoryTypeHome" :key="item.id">{{ item.type_hosue }}</option>

                              </select>
                        </div>
                  </div>

                  <div class="form-block">
                        <div class="form-group-title">Площадь:</div>
                        <div class="input-row">
                              <div class="input-item">
                                    <input class="input" type="number" placeholder="от" v-model="currentSquadFrom"/>
                                    <span>м2</span>
                              </div>
                              <div class="input-item">
                                    <input class="input" type="number" placeholder="до" v-model="currentSquadTo"/>
                                    <span>м2</span>
                              </div>
                        </div>
                  </div>

                  <div class="form-block">
                        <div class="form-group-title">Район:</div>
                        <div class="select-wrapper">
                              <select name="select-category" id="" class="select"  v-model.number="currentLocationId">
                                    <option value="0">показать все</option>
                                    <option :value="item.id" v-for="item in categoryLocation" :key="item.id">{{ item.location_sity }}</option>

                              </select>
                        </div>
                  </div>
                  <div class="form-block">
                        <div class="form-group-title">Цена:</div>
                        <div class="input-row">
                              <div class="input-item">
                                    <input class="input" type="number" placeholder="от" v-model="currentPriceFrom"/>
                                    <span>₽</span>
                              </div>
                              <div class="input-item">
                                    <input class="input" type="number" placeholder="до"  v-model="currentPriceTo"/>
                                    <span>₽</span>
                              </div>
                        </div>
                  </div>
            </div>
            <div class="filter-btn-group">
                  <button type="button" class="filter-btn-accept" @click="acceptFilter()">применить</button>
                  <button class="filter-btn-reset"  @click="resetFilter()">сбросить</button>
            </div>
      </div>

</div>
</template>

<script lang="ts">
import categoryTypeHouse from '@/data/categoryTypeHouse'
import fiterLocation from '@/data/fiterLocation'

// import {
//       Location
// } from '@/types/location'
// import {
//       CategoryTypeHouse
// } from '@/types/categoryTypeHouse'

import {
      defineComponent,
      
} from 'vue';

export default defineComponent({
    data() {
        return {
            currentCategoryId: 0,
            currentLocationId: 0,
            currentPriceFrom: 0,
            currentPriceTo: 0,
            currentSquadFrom: 0,
            currentSquadTo: 0,
            currentPage: 1
        }
    },
      props: {
        filterHouse: Number,
        filterLoc: Number,
        priceFrom: Number,
        priceTo: Number,
        squadFrom: Number,
        squadTo: Number,
        page: Number
      },

        methods: {
            acceptFilter() {
                this.$emit('update:filterHouse', this.currentCategoryId)
                this.$emit('update:filterLoc', this.currentLocationId)
                this.$emit('update:priceFrom', this.currentPriceFrom)
                this.$emit('update:priceTo', this.currentPriceTo)
                this.$emit('update:squadFrom', this.currentSquadFrom)
                this.$emit('update:squadTo', this.currentSquadTo)
                this.$emit('update:page', this.currentPage = 1)
            },

            resetFilter() {
                this.$emit('update:filterHouse', this.currentCategoryId = 0)
                this.$emit('update:filterLoc', this.currentLocationId = 0)
                this.$emit('update:priceFrom', this.currentPriceFrom = 0)
                this.$emit('update:priceTo', this.currentPriceTo = 0)
                this.$emit('update:squadFrom', this.currentSquadFrom = 0)
                this.$emit('update:squadTo', this.currentSquadTo = 0)
                this.$emit('update:page', this.currentPage = 1)
            }
        },
        computed: {
            categoryTypeHome() {
                return categoryTypeHouse
            },
            categoryLocation() {
                return fiterLocation
            }
        }
});
</script>
