<template>
<div class="favorites-box">
   <div class="favorites-block" @click="openFavoritesBody()">
      <!-- <span class="favorites-text">Избранное</span> -->
      <img class="favorites-icon" src="../../public/img/heart.svg" alt="img" />
      <span class="favorites-qnt">{{ favoritesArray.length }}</span>
   </div>
   <div class="favorites-body" :class="{active: isActive}">
      <div class="favotites-body-header">
         <p class="favotites-body-header-title">Избранное</p>
         <button class="favorites-body-close" @click="closeFavoritesBody()">закрыть</button>
      </div>
      <p class="favotites-body-message" v-if="favoritesArray.length == 0">Список пуст</p>
      <ul class="favorites-list" v-for="favorites in favoritesArray" :key="favorites.id">
         <li class="favorites-list-item">
            <article class="favorites-list-content">

               <img class="favorites-img" :src="'./img/' + favorites.image" alt="img" />
               <div class="favorites-list-info">
                  <h3 class="favorites-list-title">Район: {{ favorites.location }}</h3>
                  <h3 class="favorites-list-title">Тип: {{ favorites.type_hosue }}</h3>
                  <p class="favorites-list-text">
                     <span class="favorites-list-text-strong">Площадь:</span> {{ favorites.squad }} м2
                  </p>
                  <p class="favorites-list-text">
                     <span class="favorites-list-text-strong">Цена:</span> {{ favorites.price }}
                     ₽
                  </p>
                  <p class="favorites-list-text">
                     <span class="favorites-list-text-strong">Цена за м2:</span>
                     {{ favorites.price_to_m2 }} ₽
                  </p>

               </div>
               <img class="dellete-icon" src="../../public/img/dellete-icon.png" alt="icon" @click="delleteFavorutes(favorites.id)" />

            </article>
            <div class="favorites-body-bottom">
               <router-link :to="'/MoreDetails/' + favorites.id"> <button class="favorites-body-btn">подробнее</button></router-link>
            </div>
         </li>
      </ul>

   </div>
</div>
</template>

<script lang="ts">
import {
   defineComponent
} from "vue";

interface Data {
   isActive: boolean
}

export default defineComponent({
   props: ["favoritesArray"],
   data(): Data {
      return {
         isActive: false
      }
   },
   methods: {
      openFavoritesBody() {
         this.isActive = !this.isActive
      },
      closeFavoritesBody() {
         this.isActive = !this.isActive

      },

      delleteFavorutes(id: number) {
         this.$emit('delleteFavorutes', id)
      }
   },
   emits: ['delleteFavorutes']

})
</script>
