<template>
<div class="pagination">
   <ul class="pagination-list-item">
      <li class="pagination__item">
         <a href="#" class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': disablePreviousArrow }" aria-label="Предыдущая страница" @click.prevent="pressArrow(-1)">
            <img class="arrow-left" src="../img/arrow-left.png" alt="">
         </a>
      </li>
      <li class="paginationList" :class="{ 'active': pageNumber === page }" v-for="pageNumber in pages" :key="pageNumber" @click="paginateBtn(pageNumber)">
         {{ pageNumber }}
      </li>
      <li class="pagination__item">
         <a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': disableNextArrow }" href="#" aria-label="Следующая страница" @click.prevent="pressArrow(1)">
            <img class="arrow-left" src="../img/arrow-right.png" alt="">
         </a>
      </li>
   </ul>
</div>
</template>

    
<script>
export default {
   props: {
      page: {
         type: Number,
         required: true
      },
      count: {
         type: Number,
         required: true
      },
      perPage: {
         type: Number,
         required: true
      }
   },
   computed: {
      pages() {
         return Math.ceil(this.count / this.perPage);
      },
      disablePreviousArrow() {
         return this.page === 1;
      },
      disableNextArrow() {
         return this.page === this.pages;
      }
   },
   methods: {
      paginateBtn(page) {
         this.$emit('update:page', page);
      },
      pressArrow(val) {
         const nextPage = this.page + val;

         if (nextPage >= 1 && nextPage <= this.pages) {
            this.$emit('update:page', nextPage);
         }
      }
   }
};
</script>

<style>
.pagination__link {
   text-decoration: none;
}

.pagination__link--disabled {
   pointer-events: none;
   opacity: 0.5;
}



.active {
   background-color: #ccc;
}
</style>
