<template>
 
<div class="card-item">
   <p class="card-item-title">Каталог:</p>
   <div class="message-main" v-if="products?.length === 0">
      <span>Не найдено</span>
   </div>
   <div class="card-column">
      <div class="card-body" v-for="item in products" :key="item.id">
         <img class="card-img" :src="'./img/' + item.image" :alt="item.location" />
         <div class="card-content">

            <p class="card-location">
               Район: <span class="card-location-title">{{ item.location }}</span>
            </p>
            <p class="card-type-house">
               Тип: <span class="card-location-title">{{ item.type_hosue }}</span>
            </p>
            <p class="card-info">
               Площадь: <span class="card-info-title">{{ item.squad }} м2</span>
            </p>
            <p class="card-info">
               Цена: <span class="card-info-title">{{ item.price }} ₽</span>
            </p>
            <p class="card-info">
               Цена за м2: <span class="card-info-title">{{ item.price_to_m2 }} ₽</span>
            </p>
         </div>
         <div class="card-btn-block">
            <!-- @click="goToPage(item.id)" -->
            <router-link :to="'/MoreDetails/' + item.id"><button class="card-btn1" >подробнее</button></router-link>
            <button v-if="item.add_favorites === false" class="card-btn2" @click="addFavorites(item.id)">в избранное</button>
            <button v-if="item.add_favorites === true" :class="{cardBtnDisabled: item.add_favorites === true}" @click="addFavorites(item.id)">добавлено</button>
         </div>
         
      </div>
   </div>
</div>

</template>

<script lang="ts">
import {
   FilteredProduct
} from '@/types/filteredProduct'
import {
   FavoritesArray
} from '@/types/favoritesArray'
import {
   defineComponent,
   PropType
} from "vue";

export default defineComponent({
   props: {
      products: {
         type: Array as PropType < FilteredProduct[] > ,
      },
      favoritesArray: {
         type: Array as PropType < FavoritesArray[] >
      },

   },
   data() {
      return {
         message_main: false
      }
   },
   methods: {

      addFavorites(id: number) {
         this.$emit('addFavorites', id)

      }
   },
   emits: ['addFavorites']

})
</script>
