<template>
<div class="container">
   <a href="https://roman-zhurakovskiy.ru/"
                    ><button class="back-btn">вернуться назад</button></a
                >
   <FilterItem v-model:page="page" v-model:filterHouse="filterHouse" v-model:filterLoc="filterLoc" v-model:priceFrom="priceFrom" v-model:priceTo="priceTo" v-model:squadFrom="squadFrom" v-model:squadTo="squadTo" />
   <div class="message-body" v-if="message">
      <span>Успешно добавлено</span>
      <svg id="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
         <polyline points="9 11 12 14 22 4"></polyline>
      </svg>
   </div>

   <CardItem :favoritesArray="favoritesArray" :products="products" @add-favorites="addFavorites" />

   <PaginationPage v-model:page="page" v-model:count="countProducts" v-model:perPage="productsPerPage" />

   <FavoritesBlock :favoritesArray="favoritesArray" @dellete-favorutes="delleteFavorutes" />
</div>
</template>

<script lang="ts">
import {
   defineComponent
} from "vue";

import FilterItem from "../components/FilterItem.vue";

import CardItem from "../components/CardItem.vue";
import FavoritesBlock from "../components/FavoritesBlock.vue";
import PaginationPage from "../components/PaginationPage.vue";

import cardProduct from "../data/cardProduct";
import dataLocation from "../data/fiterLocation";

import categoryTypeHouse from "../data/categoryTypeHouse";

import {
   CardProduct
} from "../types/cardProduct";

import {
   FilteredProduct
} from "../types/filteredProduct";

import {
   Location
} from "../types/location";

import {
   FavoritesArray
} from "../types/favoritesArray";

import {
   CategoryTypeHouse
} from "../types/categoryTypeHouse";

export default defineComponent({
   name: 'app',
   components: {

      FilterItem,
      CardItem,

      FavoritesBlock,
      PaginationPage,
   },
   data() {
      return {
         cardProduct: [] as CardProduct[],
         filterLocation: [] as Location[],

         favoritesArray: [] as FavoritesArray[],
         categoryTypeHouse: [] as CategoryTypeHouse[],
         filterHouse: 0,
         filterLoc: 0,
         priceFrom: 0,
         priceTo: 0,
         squadFrom: 0,
         squadTo: 0,
         page: 1,
         productsPerPage: 8,
         message: false,
         save_id: 0
      };
   },
   created() {

      const storedFavorites = localStorage.getItem('favoritesArray');
      if (storedFavorites) {
         this.favoritesArray = JSON.parse(storedFavorites);
      }
     
   },
   mounted() {
      this.cardProduct = cardProduct;
      this.filterLocation = dataLocation;
      this.categoryTypeHouse = categoryTypeHouse;
      this.favoritesArray.forEach((elem) => {
         this.cardProduct.forEach((item) => {
         if (elem.id === item.id) {
            item.add_favorites = true;
         }
      });
      });
   },
   watch: {
      favoritesArray: {
         handler(newValue) {
            localStorage.setItem('favoritesArray', JSON.stringify(newValue));
         },
         deep: true,

      },

   },
   methods: {
      saveFavoritesToLocalStorage() {
         localStorage.setItem('favorites', JSON.stringify(this.favoritesArray));
      },
      addFavorites(id: number) {
         this.cardProduct.forEach((item) => {
            if (item.id === id) {
               this.favoritesArray.push(item);
               item.add_favorites = true;
               this.save_id = item.id
            }
         });
         this.message = true
         setTimeout(() => {
            this.message = false
         }, 1000)

      },
      delleteFavorutes(id: number) {
         this.favoritesArray = this.favoritesArray.filter(
            (favorites) => {
               return favorites.id !== id;
            },
         );
         this.cardProduct.forEach((item) => {
            if (item.id === id) {
               item.add_favorites = false;
            }
         });
      },
   },
   computed: {
      filteredProduct(): FilteredProduct[] {
         let filteredProduct = this.cardProduct;
         if (this.filterHouse) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.category_house_id === this.filterHouse;
            });
         }
         if (this.filterLoc) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.category_loc_id === this.filterLoc;
            });
         }
         if (this.priceFrom > 0) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.price >= this.priceFrom;
            });
         }
         if (this.priceTo > 0) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.price <= this.priceTo;
            });
         }
         if (this.squadFrom > 0) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.squad >= this.squadFrom;
            });
         }
         if (this.squadTo > 0) {
            filteredProduct = filteredProduct.filter((item) => {
               return item.squad <= this.squadTo;
            });
         }
         return filteredProduct;
      },
      products(): FilteredProduct[] {
         const offset = (this.page - 1) * this.productsPerPage;
         return this.filteredProduct.slice(
            offset,
            offset + this.productsPerPage,
         );
      },
      countProducts(): number {
      
         return this.filteredProduct ? this.filteredProduct.length : 0
      },
   },
});
</script>
