<template>
<div class="container">

   <div class="error-message" v-if="error_message">
      <span>Введите пустые значения форм!</span>
   </div>

   <div class="message-contact-form" v-if="message_contact_form">
      <span>Заявка успешно отправлена</span>
   </div>

   <div class="modal-wrapper" :class="{active: isActiveModal}">
      <div class="modal">
         <div class="modal__header">
            <div class="modal__title">
               Заявка на консультацию
            </div>
            <div class="modal__details">
               Тип недвижимости: {{ cardProductDetailed.type_hosue }}
            </div>
            <div class="modal__details">
               Район: {{ cardProductDetailed.location }}
            </div>
            <div class="modal__details">
               Площадь: {{ cardProductDetailed.squad }} м2
            </div>
            <div class="modal__details">
               Цена за м2: {{ cardProductDetailed.price_to_m2 }} ₽
            </div>
            <div class="modal__details">
               Площадь: {{ cardProductDetailed.price_to_m2 }} ₽
            </div>
         </div>

         <form class="modal__form" @submit.prevent="submit()">
            <div class="modal__form-content">
               <!-- formgroup -->
               <div class="formgroup">
                  <label class="modal__form-input-label" for="form-phone">
                     Имя
                  </label>
                  <input v-model="userName" type="text" id="form-name" class="modal__form-input" placeholder="Введите имя" />
               </div>
               <!-- // formgroup -->
               <!-- formgroup -->
               <div class="formgroup">
                  <label class="modal__form-input-label" for="form-phone">
                     Телефон
                  </label>
                  <input v-model="userPhone" type="text" id="form-phone" class="modal__form-input" placeholder="+7 (XXX) XXX-XX-XX" />
               </div>
               <!-- // formgroup -->

               <div class="formgroup formgroup--checkbox">
                  <input type="checkbox" id="policy" checked />
                  <label class="policy-text" for="policy">Я согласен на обработку моих персональных
                     данных. С Политикой в отношении обработки
                     персональных данных ознакомлен и
                     согласен.</label>
               </div>
               <div class="info-app">
                  <span>Так как данный проект демонстрационный функционал отправки заявки на реальную почту не реализован.</span>
               </div>
            </div>
            <input class="modal__submit" type="submit" value="Отправить заявку" />
         </form>
         <button class="modal__close" @click="modalClose()">
            Закрыть
         </button>
      </div>
   </div>

   <div class="detailes-page">
      <div class="detailed-block">
         <div class="detailed-row-top">
            <h1 class="detailed-title">Тип: {{ cardProductDetailed.type_hosue }}</h1>
            <router-link :to="'/'"> <button class="back-main-btn">вернуться назад</button></router-link>
         </div>

         <div class="detailed-info-container">
            <div class="detailed-info">
               <img class="detailed-page-img" :src="'./img/' + cardProductDetailed.image" alt="Название">
               <div class="detailed-info-params">
                  <p class="detailed-location">
                     Район: {{ cardProductDetailed.location }}
                  </p>

                  <p class="detailed-info">
                     Площадь: {{ cardProductDetailed.squad }} м2
                  </p>
                  <p class="detailed-info">
                     Цена: {{ cardProductDetailed.price }} ₽
                  </p>
                  <p class="detailed-info">
                     Цена за м2: {{ cardProductDetailed.price_to_m2 }} ₽
                  </p>
               </div>
            </div>

         </div>
         <div class="detailed-info-description">
            <h2>
               {{ cardProductDetailed.description }}
            </h2>
         </div>
         <div class="detailed-info-contacts">

            <div class="form">
               <button @click="submitApplication()">ОСТАВИТЬ ЗАЯВКУ</button>
            </div>
            <div>
               <p>Оставьте контактные данные и получите бесплатную консультацию по подбору недвижимости у наших экспертов</p>
            </div>
         </div>
      </div>
   </div>
</div>
</template>

<script lang="ts">
import cardProduct from "../data/cardProduct";
import {
   CardProduct
} from "../types/cardProduct";
import {
   defineComponent,
} from "vue";
interface cardProductDetailed {
   id: number;
   category_house_id: number;
   category_loc_id: number;
   type_hosue: string;
   location: string;
   squad: number;
   price: number
   price_to_m2: number;
   image: string;
   add_favorites: boolean;
   description: string;
}
export default defineComponent({

   data() {
      return {
         cardProduct: [] as CardProduct[],
         isActiveModal: false,
         message_contact_form: false,
         userName: '',
         userPhone: '',
         error_message: false
      }
   },
   methods: {
      submitApplication() {
         this.isActiveModal = true
      },
      submit() {
         if (this.userName != '' && this.userPhone != '') {
            this.isActiveModal = false
            this.message_contact_form = true

         } else {
            this.error_message = true
         }
         setTimeout(() => {
            this.error_message = false
         }, 2000)
         setTimeout(() => {
            this.message_contact_form = false
         }, 2000)
         this.userName = ''
         this.userPhone = ''
      },
      modalClose() {
         this.isActiveModal = false
      }
   },
   computed: {
      cardProductDetailed(): cardProductDetailed | any {
         let cardProductObj = cardProduct.find(item => item.id === Number(this.$route.params.id));

         return cardProductObj;
      }
   }

})
</script>
